<template>
  <v-container
    id="user-profile-view"
    fluid
    tag="section"
  >
    <v-overlay :value="showUpdate">
      <v-alert
        icon="mdi-check"
        prominent
        shaped
        type="success"
        transition="scale-transition"
        class="position-fixed"
      >
        Perfil atualizado com sucesso!
      </v-alert>
    </v-overlay>
    <v-overlay :value="showError">
      <v-alert
        icon="mdi-alert"
        prominent
        shaped
        type="error"
        transition="scale-transition"
        class="position-fixed"
      >
        Algo de errado não está certo.
      </v-alert>
    </v-overlay>

    <v-row justify="center">
      <v-col
        cols="12"
        md="8"
      >
        <material-card>
          <v-row>
            <v-col cols="2">
              <v-hover v-slot="{ hover }">
                <label for="file-input">
                  <v-img
                    class="rounded-circle mt-n16 ms-6 profile-pic"
                    align="center"
                    width="125"
                    :aspect-ratio="1/1"
                    :src="profilePic"
                    style="cursor: pointer"
                  >
                    <v-fab-transition>
                      <v-icon
                        v-if="hover"
                        class="fill-height"
                        large
                        dark
                      >
                        mdi-camera
                      </v-icon>
                    </v-fab-transition>
                  </v-img>
                </label>
              </v-hover>
              <input
                v-show="false"
                id="file-input"
                type="file"
                accept="image/jpeg"
                @change="onFileChange"
              >
            </v-col>
            <v-col cols="6">
              <h3 class="font-weight-medium ms-5">
                Editar Perfil — <small class="text-body-1">Atualize suas informações</small>
              </h3>
            </v-col>
          </v-row>

          <v-form>
            <v-container class="py-0">
              <v-row>
                <v-col
                  cols="12"
                  md="3"
                >
                  <v-text-field
                    disabled
                    label="Empresa"
                    value="NVS Licenças"
                    outlined
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="3"
                >
                  <v-text-field
                    v-model="items.rua"
                    disabled
                    color="blue"
                    label="CPF"
                    outlined
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="6"
                >
                  <v-text-field
                    v-model="items.email"
                    :readonly="edit"
                    color="blue"
                    label="Endereço de E-mail"
                    outlined
                    :append-icon="edit ? 'mdi-pencil' : 'mdi-pencil-off'"
                    @click:append="edit = !edit"
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="6"
                >
                  <v-text-field
                    v-model="items.user"
                    disabled
                    color="blue"
                    label="Nome"
                    outlined
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="6"
                >
                  <v-text-field
                    v-model="items.sobrenome"
                    disabled
                    color="blue"
                    label="Sobre Nome"
                    outlined
                  />
                </v-col>

                <v-col cols="9">
                  <v-text-field
                    v-model="items.logradouro"
                    color="blue"
                    label="Logradouro"
                    outlined
                    disabled
                  />
                </v-col>

                <v-col cols="3">
                  <v-text-field
                    v-model="items.numero"
                    color="blue"
                    label="Número"
                    outlined
                    :append-icon="edit2 ? 'mdi-pencil' : 'mdi-pencil-off'"
                    @click:append="edit2 = !edit2"
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="4"
                >
                  <v-text-field
                    v-model="items.estado"
                    color="blue"
                    label="Estado"
                    outlined
                    disabled
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="4"
                >
                  <v-text-field
                    v-model="items.cidade"
                    color="blue"
                    label="Cidade"
                    outlined
                    disabled
                  />
                </v-col>

                <v-col
                  cols="12"
                  md="4"
                >
                  <v-text-field
                    v-model="items.cep"
                    v-mask="'########'"
                    :rules="rulesCEP"
                    required
                    color="blue"
                    name="cep"
                    label="CEP"
                    outlined
                    :append-icon="edit3 ? 'mdi-pencil' : 'mdi-pencil-off'"
                    @click:append="edit3 = !edit3"
                    @keyup="preencherCep"
                  />
                </v-col>

                <v-col cols="12">
                  <v-textarea
                    v-model="items.sobre"
                    color="blue"
                    label="Sobre Mim"
                    outlined
                  />
                </v-col>

                <v-col
                  cols="12"
                  class="text-right"
                >
                  <v-btn
                    color="primary"
                    min-width="150"
                    @click="editProfile"
                  >
                    Atualizar Perfil
                  </v-btn>
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </material-card>
      </v-col>

      <v-col
        cols="12"
        md="4"
      >
        <app-card class="mt-4 text-center">
          <v-img
            class="rounded-circle elevation-6 mt-n12 d-inline-block"
            src="../assets/brayan-ruan.png"
            width="125"
          />

          <v-card-text class="text-center">
            <h6 class="text-h6 mb-2 text--black">
              CEO / CO-FUNDADOR
            </h6>

            <h4 class="text-h4 mb-3 text--primary">
              Brayan Ruan
            </h4>

            <p class="text--black text-justify">
              Vindo de família humilde, Brayan sempre batalhou firme para conquistar seu espaço e não mede esforços para conquistar seus objetivos. Trabalha com licenças desde seus 14 anos de idade, quando junto ao seu pai fundou a NVS.<br>
            </p>
            <p class="text--black text-justify">
              Após 20 anos no mercado, a pequena empresa que surgiu numa sala de escritório agora é a maior do Sul do país graças à garra e determinação não só do Brayan, mas também de cada pessoa que <strong>fez</strong> e <strong>faz</strong> parte dessa história.
            </p>
            <v-btn
              href="https://www.instagram.com/brayanruan94/"
              target="_blank"
              class="mr-0"
              color="primary"
              min-width="100"
              rounded
            >
              Seguir
            </v-btn>
          </v-card-text>
        </app-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios'
  // import resizeImage from '@/plugins/image-resize.js'

  export default {
    name: 'UserProfileView',

    data: () => ({
      edit: true,
      edit2: true,
      edit3: true,
      erroCep: false,
      showUpdate: false,
      showProfileUpdate: false,
      showError: false,
      originalImg: '',
      profilePic: window.localStorage.img,
      icon: [
        { text: 'mdi-camera' },
      ],
      items: {
        logradouro: '',
        estado: '',
        cidade: '',
        rua: window.localStorage.rua,
        user: window.localStorage.user,
        sobrenome: window.localStorage.sobrenome,
        email: window.localStorage.email,
        cep: window.localStorage.cep,
        numero: window.localStorage.numero,
        sobre: window.localStorage.sobre,
      },
    }),

    computed: {
      rulesCEP () {
        const rules = []

        const rule = (this.erroCep === false && this.items.cep.length === 8) || 'CEP inválido'
        rules.push(rule)

        return rules
      },
    },

    watch: {
      items: {
        handler () {
          window.localStorage.rua = this.items.rua
          window.localStorage.user = this.items.user
          window.localStorage.sobrenome = this.items.sobrenome
          window.localStorage.email = this.items.email
          window.localStorage.cep = this.items.cep
          window.localStorage.numero = this.items.numero
          window.localStorage.sobre = this.items.sobre
        },
        deep: true,
      },
    },

    beforeMount () {
      this.preencherCep()
    },

    methods: {
      getCep (cep) {
        return axios.get(`https://viacep.com.br/ws/${cep}/json/`)
      },
      preencherCep () {
        const cep = this.items.cep.replace(/\D/g, '')
        if (cep.length === 8) {
          this.getCep(cep)
            .then(r => {
              this.items.logradouro = r.data.logradouro
              this.items.estado = r.data.uf
              this.items.cidade = r.data.localidade
              'erro' in r.data ? this.erroCep = true : this.erroCep = false
            })
        }
      },

      // blob storage
      // readURL (file) {
      //   const reader = new FileReader()
      //   reader.onload = (e) => this.originalImg = e.target.result
      //   reader.readAsDataURL(file) // convert to base64 string
      //   resizeImage({ file: file, maxSize: 150 }).then((resizedImage) => {
      //     this.profilePic = URL.createObjectURL(resizedImage)
      //   }).catch((err) => {
      //     console.error(err)
      //   })
      //   window.localStorage.img = this.originalImg
      // },
      set (key) {
        try {
          localStorage.setItem(key, this.profilePic)
        } catch (e) {
          console.log(`Falha no armazenamento: ${e}`)
        }
      },
      onFileChange (e) {
        var files = e.target.files || e.dataTransfer.files
        if (!files.length) {
          return
        }
        this.createImage(files[0])
      },
      createImage (file) {
        var reader = new FileReader()

        reader.onload = (e) => {
          this.profilePic = e.target.result
          this.set('img')
        }
        reader.readAsDataURL(file)
      },

      editProfile () {
        axios.put(`https://sheetdb.io/api/v1/khm5ici88y1u4/RUA/${this.items.rua}`, {
          data: {
            EMAIL: `${this.items.email}`,
            NUMERO: `${this.items.numero}`,
            CEP: `${this.items.cep}`,
            SOBRE: `${this.items.sobre}`,
            IMAGE: `${this.profilePic}`,
          },
        })
          .then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showUpdate = !this.showUpdate
              setTimeout(() => {
                this.showUpdate = !this.showUpdate
              }, 3500)
            } else {
              this.showError = !this.showError
              setTimeout(() => {
                this.showError = !this.showError
              }, 3500)
            }
          })
      },
    },
  }
</script>

<style scoped>
  .profile-pic:hover {
    opacity: 0.6;
  }
</style>
